.header-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 25px;
    border-bottom: 1px solid #818181;
}

.header-logo {
    font-size: 24px;
    font-family: var(--bagel-font);
}

.header-menus {
    display: flex;
    gap: 10px;
    font-size: 16px;
    text-align: center;
}

.header-menu {
    width: 80px;
    color: var(--secondary1000);
    font-weight: 300;
    cursor: pointer;
}

.header-menu-selected {
    width: 80px;
    color: var(--black);
    font-weight: 900;
    cursor: pointer;
}

.hamburger-button {
    display: none; 
    cursor: pointer;
}

.hamburger-button .bar {
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 5px 0;
}

.hamburger-menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* 반응형 처리 */
@media (max-width: 768px) {
    .header-menus {
        display: none; 
    }

    .hamburger-button {
        display: block; 
    }

    .hamburger-menu {
        display: flex;
        position: absolute;
        top: 60px;
        right: 0;
        background-color: #fff;
        padding: 20px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    }
}
